const Player = ({user}) => (
    <div className="player container">
        <div  className="player username">
            <Link to={`/Profilepage/${user.id}`} style={{ color: 'green' }}>
                username: {user.username}
            </Link>
    {/*<Link to="/Profilepage" style={{ color: 'green' }}>   username: {user.username} </Link>*/}
        </div>
        <div className="player id">name: {user.name}</div>
        <div className="player id">creationtime: {user.creationtime}</div>
        <div className="player id">status: {user.status}</div>
        {/*<div className="player id">token: {user.token}</div>*/}
        {/*<div className="player name">{user.name}</div>*/}
        <div className="player id">id: {user.id}</div>
    </div>
);

Player.propTypes = {
    user: PropTypes.object
};

const UsersOverview = () => {
    // use react-router-dom's hook to access the history
    const history = useHistory();
    // define a state variable (using the state hook).
    // if this variable changes, the component will re-render, but the variable will
    // keep its value throughout render cycles.
    // a component can have as many state variables as you like.
    // more information can be found under https://reactjs.org/docs/hooks-state.html
    const [users, setUsers] = useState(null);

    const Logout = useCallback(() => {
        try {

        let token;
        token = localStorage.getItem('token')

        const requestBody = JSON.stringify({token});
        const response = api.post('/users/loginout', requestBody);
        Promise.all([response]).then((values) => {
            message.error('UserName:'+values[0].data+' has quit.');
            localStorage.removeItem('token');
            history.push('/login');
            console.log(values[0].data+"out");
        });
    }catch (error){
        message.error('has quit.');
        localStorage.removeItem('token');
        history.push('/login');
        console.log(error);
    }
        localStorage.removeItem('token');
        history.push('/login');

    },[history]);

    // the effect hook can be used to react to change in your component.
    // in this case, the effect hook is only run once, the first time the component is mounted
    // this can be achieved by leaving the second argument an empty array.
    // for more information on the effect hook, please see https://reactjs.org/docs/hooks-effect.html
    useEffect(() => {
        // effect callbacks are synchronous to prevent race conditions. So we put the async function inside:
        async function fetchData() {
            try {
                const response = await api.get('/users/overview');

                // delays continuous execution of an async operation for 1 second.
                // This is just a fake async call, so that the spinner can be displayed
                // feel free to remove it :)
                await new Promise(resolve => setTimeout(resolve, 1000));
                console.log('request to:', response);
                // Get the returned users and update the state.
                setUsers(response.data);

                // This is just some data for you to see what is available.
                // Feel free to remove it.
                console.log('request to:', response.request.responseURL);
                console.log('status code:', response.status);
                console.log('status text:', response.statusText);
                console.log('requested data:', response.data);

                // See here to get more data.
                console.log(response);
            } catch (error) {
                console.error(`Something went wrong while fetching the users: \n${handleError(error)}`);
                console.error("Details:", error);
                alert("Something went wrong while fetching the users! See the console for details.");
                Logout();
            }
        }

        fetchData();
    }, [Logout]);

    let content = <Spinner/>;

    if (users) {
        content = (
            <div className="game">
                <ul className="game user-list">
                    {users.map(user => (
                        // <Player user={user} key={user.name}/>
                        // <Player user={user} key={user.creationTime}/>
                        // <Player user={user} key={user.status}/>
                        <Player user={user} key={user.id}/>
                    )

                    )}
                </ul>

                <Button
                    width="100%"
                    onClick={() => Logout()}
                >
                    Logout
                </Button>
            </div>
        );
    }

    return (
        <BaseContainer className="game container">
            <h2>Happy Coding!</h2>
            <p className="game paragraph">
                Get all users from secure endpoint:
            </p>
            {content}
        </BaseContainer>
    );
}

export default UsersOverview;
